:root {
  --background-color: #ac2432;
  --font-red: #c51230;
  --btn-unhover-color: #c2c2c2;
  --gray: #555555;
  /* --window-scrollY: 0; */
}

.flexbox {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  flex-direction: row;
}

.menu {
  background-color: white;
  height: calc(5.75px + 2.2vw);
  justify-content: space-evenly;
  margin: 0;
  /* text-decoration: none; */
}

.menuItem {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  flex-direction: column;
  display: flex;
  margin-top: -0.1vw;
  border-style: none;
}

.menuItem::after {
  content: '';
  width: 100%;
  height: calc(1.25px + 0.3vw);
  background-color: white;
  transition: background-color 0.5s ease-in;
}

.menuItem:hover::after {
  background-color: #ac2432;
  background-color: var(--background-color);
}

.menuLink {
  text-decoration: none;
  color: #c51230;
  color: var(--font-red);
  font-family: "Open Sans";
  font-weight: 100;
  font-size: calc(3.75px + 0.9vw);
}
.footer {
  justify-content: space-evenly;
  margin-top: 3vw;
}

.footerItem {
  display: flex;
}

.footerLink {
  background-color: var(--background-color);
  color: var(--btn-unhover-color);
  font-family: "Open Sans";
  font-weight: 100;
  font-size: calc(4px + 0.9vw);
  text-decoration: none;
}

.footerLink:hover {
  color: white;
}

.footerP {
  color: white;
  font-family: "Open Sans";
  font-weight: 700;
  font-size: calc(6px + 0.5vw);
  font-size: calc(4px + 0.9vw);
}
.fullWidth {
  width: calc(100vw + 16px);
  margin-left: -16px;
}

.flexbox {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  flex-direction: row;
}

.wordWrap {
  word-wrap: break-word;
}

.detailTitle {
  margin-top: 3vw;
  justify-content: flex-start;
}

.detailTitle p {
  margin: 0;
}

.shirtDetail {
  align-items: flex-start;
  justify-content: flex-start;
  grid-gap: 4vw;
  gap: 4vw;
  margin: 3vw;
}

.shirtDetail img {
  width: calc(40px + 26vw);
}

.details {
  width: 50vw;
  flex-direction: column;
  padding: 0;
  justify-content: flex-start;
  align-items: flex-start;
}

.details > div {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  /* align-content: flex-start; */
  justify-content: flex-start;
}

.price {
  color: var(--font-red);
  font-size: calc(10px + 1.5vw);
  margin: 0;
}

.description {
  color: var(--font-red);
  font-size: calc(6px + 0.9vw);
}

.select p {
  letter-spacing: -1px;
  font-size: calc(6px + 0.9vw);
}

.select > div {
  justify-content: flex-start;
}

.select > div > p {
  font-size: medium;
}

.detailBtn {
  font-size: calc(6px + 0.9vw);
  border: none;
  cursor: pointer;
  transition: color 0.2s ease;
}

/* .detailBtn:hover {
  color: white;
} */

.coloredBtn {
  background-color: rgb(255, 255, 255);
  border-style: solid;
  border-color: var(--background-color);
  border-width: 2px;
  color: black;
}

.quantityDropdown {
  background-color: rgb(255, 255, 255);
  border-style: solid;
  border-color: var(--background-color);
  border-width: 2px;
  border-radius: calc(5px + 0.4vw);
  
  margin: 6px;
  padding: 0 18px 0 18px;
  height: calc(36px + 1.2vw);
  /* color: var(--btn-unhover-color); */
  color: black;
  font-family: “Open Sans”;
  font-size: calc(6px + 0.9vw);
  text-decoration: none;
  cursor: pointer;
}

.disableBtn:disabled {
  background-color: #8d8d8d;
  cursor: default;
  pointer-events: none;
}

.textMedium {
  font-size: medium;
}
.cartItems {
  /* background-color:aqua; */
  width: 50vw;
}

.cartItem {
  width: 50vw;
  border-top: solid 1px var(--background-color);
}

.cartItemDesc > div {
  margin: 0.5vw;
}

.cartItemDesc > div p {
  margin: 3px;
}

.fee {
  background-color:bisque;
  width: 30vw;
  border: solid 1px var(--background-color);
  border-radius: 10px;
  padding: 1vw;
  /* padding-right: 2vw; */
}

.fee p {
  margin-top: 0.5vw;
